@media only screen and (max-width : 768px) {
  .widget{
    width: 320px;
  }
  .widget .weatherInfo {
    flex: 0 0 60%;
    height: 50%;
    width: 40px;
    background: black;
    display: flex;
    align-items: center;
    font-weight: 40px;
  }
  .widget .date {
    flex: 0 0 40%;
    height: 50%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    font-size: 15px;
    font-weight: 700;
    text-align: center;
  }
  .widget .weatherIcon{
    font-size: 55px;
  }
  .widget .weatherInfo .temperature{
    font-size: 25px;
  }
  .widget .weatherInfo .description {
    font-weight: 15px;
  }
  .widget .weatherInfo .description .weatherCondition{
    font-size: 20px; 
  }
  .widget .weatherInfo .description .place {
    font-size: 12px;
  }
  .widget .extra-temp p {
    font-size: 15px;
    font-weight: 600;
  }
  .widget .extra-temp .extra-info-leftside {
    font-size: 10px;
    font-weight: 600;
  }
  .searchTerm {
    width: 230px;
    /* border: 3px solid #00b4cc; */
    border: none;
    padding: 20px;
    height: 20px;
    border-radius: 5px 0 0 5px;
    outline: none;
    font-size: 18px;
  }
  .wrap {
    display: flex;
    justify-content: center;
    text-align: center;
    margin-top: -100px;
    margin-bottom: 30px;
    /* position: relative;  */
    
  }
}
